body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #f2f2f2;
  text-align: center;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 5%;
  background-color: /* #333333*/ #1a75ff;
  color: #f9f9f9;
}

i {
  font-size: 40px;
  margin-right: 1%;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.todoList {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 40%;
  padding:1%;
  margin-bottom: 2%;
  background-color: 	#f9f9f9;
  border: 1px solid black;
}

.del-btn {
  background-color: 	#DC143C;
  color: #fff;
  border: 2px solid 	#DC143C;
  font-size: 17px;
}

button:hover {
  cursor: pointer;
}

.form {
  margin-top: 5%;
  text-align: center;
}

.myform {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.form input {
  height: 30px;
  width: 250px;
  margin-right: 0.3%;
}

.form button {
  color: #f9f9f9;
  background-color: #1a75ff;
  font-size: 16px;
  height: 36px;
  border: none;
}


